//       @include badge-variant-soft($value);
//   }
// }

// Outline badge
@mixin badge-variant-outline($bg) {
  color: $bg;
  border: 1px solid $bg;
  background-color: transparent;
}

@each $color, $value in $theme-colors {
  .badge-outline-#{$color} {
    @include badge-variant-outline($value);
  }
}

.badge {
  &.bg-light,
  &.badge-soft-light,
  &.badge-outline-light {
    color: $dark;
  }
}

// Dark badge

.badge.bg-dark {
  color: $light;
}
