//
// emoji-picker.scss
//

.fg-emoji-picker {
  background-color: var(--#{$prefix}secondary-bg) !important;
  width: 250px !important;
  box-shadow: $box-shadow !important;
  top: auto !important;
  bottom: 70px;
  * {
    font-family: $font-family-base !important;
    color: $body-color !important;
  }
  @media (max-width: 991.98px) {
    left: 14px !important;
    top: auto !important;
    bottom: 60px;
  }

  .fg-emoji-picker-container-title {
    color: var(--#{$prefix}dark) !important;
  }

  .fg-emoji-picker-search {
    height: 40px !important;
    input {
      background-color: $input-bg !important;
      color: $input-color !important;
      border: 1px solid $border-color;
      padding: $input-btn-padding-y $input-btn-padding-x !important;
      font-size: $font-size-base !important;
      &::placeholder {
        color: $input-placeholder-color !important;
      }
    }

    svg {
      fill: var(--#{$prefix}body-color) !important;
      right: 11px;
      top: 12px;
    }
  }

  .fg-emoji-picker-categories {
    background-color: var(--#{$prefix}light) !important;
    li.active {
      background-color: rgba(var(--bs-primary-rgb), 0.2);
    }
    a {
      &:hover {
        background-color: rgba(var(--bs-primary-rgb), 0.2);
      }
    }
  }
}

.fg-emoji-picker-grid > li:hover {
  background-color: rgba(var(--bs-primary-rgb), 0.2) !important;
}

a.fg-emoji-picker-close-button {
  background-color: var(--#{$prefix}light) !important;
}

.fg-emoji-picker-categories svg {
  fill: var(--#{$prefix}body-color) !important;
}

.fg-emoji-picker-grid > li {
  background-color: var(--#{$prefix}secondary-bg) !important;
}
